<template>
	<div v-my-directive data-title="用户中心" class="usermain">
		<!-- 左边 -->
		<userleft></userleft>
		<div class="CatalogLayout">
			<!-- 引入文件的方式 -->
			<div class="Genericpadding bold">我的訂單</div>
			<div class="Genericpadding helf CatalogLayout">
				<div class="CatalogLayout minaxgap" v-loading="loading">
					<el-radio-group class="center" v-model="tabPosition" style="margin-bottom: 20px">
						<el-radio-button label="" @click=";(tabPosition = ''), (curPage = 1), actr()">全部</el-radio-button>
						<el-radio-button label="1" @click=";(tabPosition = '1'), (curPage = 1), actr()">已支付</el-radio-button>
						<el-radio-button label="0" @click=";(tabPosition = '0'), (curPage = 1), actr()">未支付</el-radio-button>
						<el-radio-button label="2" @click=";(tabPosition = '2'), (curPage = 1), actr()">已关闭</el-radio-button>
					</el-radio-group>
					<el-table :data="aclist" border style="width: 100%">
						<el-table-column prop="OrderNo" label="订单号" width="185" />
						<el-table-column prop="name" label="名称" width="180" show-overflow-tooltip />
						<el-table-column prop="Orderdetails.Price" width="60" label="金额" />
						<el-table-column prop="Status" width="70" label="状态" />
						<el-table-column prop="updatetime" label="时间" width="150" show-overflow-tooltip />
						<!--  -->
						<el-table-column prop="Orderdetails.Price" label="操作" />
					</el-table>
					<div class="dispaygrid">
						<el-pagination background class="centerend" :hide-on-single-page="value" :current-page="curPage" @current-change="page" :page-size="pagesize" layout="prev, pager, next" :total="total"> </el-pagination>
					</div>
				</div>
			</div>
		</div>
		<!-- 订单号 -->
		<settlement v-if="Purchasecourses" :info="Order" :key="key"></settlement>
	</div>
</template>

<script>
import settlement from '@/components/settlement'
import userleft from './../components/userleft'
import { dataRequest as api } from '@/api/api'
import { mapState } from 'vuex'
export default {
	name: 'name',
	props: ['userid'],
	computed: {
		...mapState(['user']),
	},
	components: {
		// 组件
		userleft,
		settlement,
	},
	data() {
		return {
			//返回值
			tabPosition: '',
			Order: {},
			Purchasecourses: false,
			key: 0,
			loading: true,
			value: true,
			curPage: 1,
			pagesize: 1, //每页显示数量
			total: 0,

			//返回值
			offset: 'DESC',
			tosort: 'time',
			aclist: [],
		}
	},
	methods: {
		// 函数
		// 函数
		async actr() {
			this.loading = true
			let condition = { user: this.user.id }
			if (this.tabPosition) {
				condition.Status = this.tabPosition
			}
			let array = await api(['post=getOrderpage', { condition: condition, perPage: this.pagesize, page: this.curPage, offset: this.offset, tosort: this.tosort }])
			console.log('结果', array)

			if (array.code == 200) {
				this.aclist = array.data
				this.total = array.massage / 1
			} else {
				this.aclist = []
				this.total = 0
			}
			this.loading = false
		},
		// fenye
		page(ele) {
			this.loading = true
			this.curPage = ele
			this.actr()
		},
	},
	mounted() {
		// 生命周期:

		this.actr()
	},
	updated() {
		window.scrollTo({ behavior: 'instant', top: 0 })
	},
}
</script>
<style scoped>
.centerend {
	place-self: center end;
}
.pimgmins .image-slot {
	font-size: 8px;
}
</style>
